body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: lato, sans-serif;
    margin-left: 5%;
    margin-right: 5%;
    margin-bottom: 0%;
    margin-top:1%;
    background: #282c34;
    font-size: 12pt;
}

.change-logs {
    font-family: lato, sans-serif;
    margin-left: 5%;
    margin-right: 5%;
    margin-bottom: 0%;
    margin-top:5%;
    background: #282c34;
}

h1 {
    color: #5ec9e7;
    -webkit-animation: fadeInLeft;
            animation: fadeInLeft;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.h1-change-logs {
    color: #5ec9e7;
    -webkit-animation: fadeInDown;
            animation: fadeInDown;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    text-align: center;
}

p {
    color: #5ec9e7;
    text-align: center;
    transition: 0.5s cubic-bezier(.55, 0, .1, .1);
    -webkit-animation: fadeInDown;
            animation: fadeInDown;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}
.underline-change-logs{
    color: #5ec9e7;
    text-align: center;
    position: relative;
}
.underline-change-logs::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #EEEEEE;
    visibility: visible;
    -webkit-transform: scaleX(.1);
            transform: scaleX(.1);
    transition: all 0.3s ease-in-out;
}

.underline {
    color: #5ec9e7;
    text-align: center;
    transition: 0.5s cubic-bezier(.55, 0, .1, .1);
    -webkit-animation: fadeInDown;
            animation: fadeInDown;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    position: relative;
}
.underline:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #EEEEEE;
    visibility: hidden;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: all 0.3s ease-in-out;
}
.underline:hover:before {
visibility: visible;
-webkit-transform: scaleX(.1);
        transform: scaleX(.1);
}

.list ul li {
    color: #EEEEEE;
    text-align: left;
}

.list-link {
    text-decoration: none;
    color: #EEEEEE;
}

.list-link li {
    list-style: none;
}

.list-link li a {
    color: #5ec9e7;
    text-decoration: none;
}

.strike-done {
    display: inline-block;
	position: relative;
	transition: all 0.5s cubic-bezier(.55, 0, .1, 1);
}
.strike-done::after {
    content: '';
	position: absolute;
	display: block;
	width: 100%;
	height: 1px;
	box-shadow: 0 1px rgba(255,255,255,0.6);
	margin-top: -0.7em;
	background: #EEEEEE;
	-webkit-transform-origin: center left;
	        transform-origin: center left;
	-webkit-animation: strikethrough 1s 0.5s cubic-bezier(.55, 0, .1, 1) 1;
	        animation: strikethrough 1s 0.5s cubic-bezier(.55, 0, .1, 1) 1;
	transition: -webkit-transform 0.5s cubic-bezier(.55, 0, .1, 1);
	transition: transform 0.5s cubic-bezier(.55, 0, .1, 1);
	transition: transform 0.5s cubic-bezier(.55, 0, .1, 1), -webkit-transform 0.5s cubic-bezier(.55, 0, .1, 1);
}
.strike-done:hover {
	color: lightgreen;
	background: rgba(255, 189, 182, 0.3);
}
.strike-done:hover:after {
	-webkit-transform: scaleX(0);
	        transform: scaleX(0);
	-webkit-transform-origin: center right;
	        transform-origin: center right;
}
@-webkit-keyframes strike-done {
	from {
		-webkit-transform: scaleX(0);
		        transform: scaleX(0);
	}
	to {
		-webkit-transform: scaleX(1);
		        transform: scaleX(1);
	}
}
@keyframes strike-done {
	from {
		-webkit-transform: scaleX(0);
		        transform: scaleX(0);
	}
	to {
		-webkit-transform: scaleX(1);
		        transform: scaleX(1);
	}
}
.strike-todo {
    display: inline-block;
	position: relative;
	transition: all 0.5s cubic-bezier(.55, 0, .1, 1);
}
.strike-todo:hover {
	color: orangered;
	background: rgba(255, 189, 182, 0.3);
}
.App {
  text-align: center;
  height: 90vh;
  /* width: 90vw; */
  margin: 2rem auto;
  display: grid;
  grid-template-areas: "header header header"
                        "left main right"
                        "footer footer footer";
  grid-template-rows: 1fr 3fr 1fr;
  grid-template-columns: 1fr 2fr 1fr;
  background-color: #282c34;
  color: white;
  font-family: 'Press Start 2P', cursive;
}

.App-logo {
  width: 350px;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}
.App-link:visited {
  color: #61dafb;
}
.App-link:hover {
  color: cadetblue;
}

.cell-header {
  grid-area: header;
}
.cell-left {
  background: orangered;
  grid-area: left;
}
.cell-main {
  background: limegreen;
  grid-area: main;
}
.cell-right {
  background: gold;
  grid-area: right;
}
.cell-footer {
  background: blueviolet;
  grid-area: footer;
  padding: 1%;
}
.cell {
  color: white;
  font-size: small;
  text-align: center;
  padding: 3rem;
}


